<template>
  <div>
    <el-table
      :data="tableData"
      border>
      <el-table-column
        label="姓名">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.username}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="用户名">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.idCard}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="类型id">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.branchId}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="类型名称">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.branchName}}</span>
        </template>
      </el-table-column>

      <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
        @click="delData(scope.row._id)">删除</el-button>
      </template>
    </el-table-column>
    </el-table>
    <pagination @pageSizeChange="pageSizeChange"  @currentPageChange="currentPageChange"></pagination>
  </div>
</template>

<script>
  import pagination from '@/components/pagination'
  export default {
    name: "users",
    components:{
      pagination
    },
    data() {
      return {
        tableData:[],
        params:{
          pageSize: 5,
          page: 1
        }

      }
    },
    methods:{
      getdata(){
        this.$axios.post('getusers',this.params,{withCredentials: true}).then((res)=>{
          this.tableData = res.data
        })
      },
      delData(val){
        this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(()=>{
          this.$axios.post('delusers',{id:val}).then((res)=>{
            console.log(res)
            if(res.code==200){
              this.$message({
                message: '用户删除成功',
                type: 'success'
              });
              setTimeout(()=>{
                this.$router.go(0)
              },1000)
            }
          })
        }).catch(()=>{
          console.log(res.err)
        })

      },
      pageSizeChange(val){
        this.params.pageSize = val;
        this.getdata()
      },
      currentPageChange(val){
        this.params.page = val;
        this.getdata()
      }
    },
    created(){
      this.getdata()
    }
  }
</script>

<style scoped>
  .img-item{
    width: 100px;
    max-height: 100px;
  }
</style>



